@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500;600&family=Raleway:wght@500&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    min-height: 100vh;
    width: 100vw;
    overflow-x: hidden;
    overflow-y: auto;
    font-family: 'Poppins', sans-serif;
}

.heading{
    display: flex;
    justify-content: center;
    align-items: center;
}
.title{
    font-size: 3rem;
    margin: 6vh;
    border: 7px dashed rgb(1, 17,160);
    border-radius: 20px;
    padding: 1vh 5vw;
}
@media (max-width:550px) {
    .title{
        font-size: 1.5rem;
    }
}
.cards{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    max-width: 1300px;
    margin: 0 auto;
}
.card{
    max-width: 400px;
    width: 90%;
    height: max-content;
    padding: 1rem;
    margin: 1rem;
    display: flex;
    align-items: center;
    flex-direction: column;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.image{
    width: 99%;
    aspect-ratio: 1/1;
    object-fit: cover;
}
.tour-info{
    margin: 20px 5px;
}
.tour-price{
    color: #1faa59;
    font-size: 1.3rem;
}
.tour-name{
    font-size: 1.5rem;
}
.reedMore{
    color: #12b0e8;
    cursor: pointer;
}
.btnRed{
    margin-top: 18px;
    padding: 10px 80px;
    border: 1ox solid #b4161b;
    cursor: pointer;
    border-radius: 10px;
    font-size: 18px;
    font-weight: bold;
    background-color: #b4262b21;
}
.btnRed:hover{
    background-color: red;
    color: white;
    transition: all 0.2s;
}
.refresh{
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.refreshBtn{
    margin-top: 18px;
    padding: 10px 80px;
    border: 1px solid white;
    cursor: pointer;
}